<template>
  <div class="partprofile">
    <ProfileRow :profileInfo='profileInfo'></ProfileRow>
  </div>
</template>


<script>
// @ is an alias to /src
import ProfileRow from '@/components/row/ProfileRow.vue'
import * as request from '../../network/request'



export default {
  name: 'PartProfile',
  components: {
    ProfileRow,
  },
  data(){
    return{
      profileInfo:{
        paragraph:'',
        menuList:[],
        iMsg:'',
        iMsg2:'',
        imsgt:'',
        heightList:[]
      }
    }
  },
  computed:{
    pid(){
      if(this.$route.query.type=='me')
        {return 0}
      if(this.$route.query.type=='page')
        {return 1}
      if(this.$route.query.type=='work')
        {return 2}
      if(this.$route.query.type=='skill')
        {return 3}
      else{return -1}
    }
  },
  methods:{
    refreshData(){
    request.request({
      url:'http://39.107.227.252/getParagraphDetail/',
      data:{'pid': this.pid,},
      method: 'post',
    }).then(res =>{
      this.profileInfo = res.data
    }).catch(err =>{
      console.log(err);
    })
    }
  },
  watch:{
    '$route': 'refreshData'
  },
  created() {
    this.refreshData()
  }
}

</script>
<style>
.partprofile{
  min-height: 700px;
}
</style>